// 看板图1
export function MapOne(myCharts, datas) {
  // 1实例化对象
  var myChart = myCharts
  // 2. 指定配置项和数据
  var option = {
    color: ['#c487ee', '#deb140', '#49dff0'],
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b}: {c} ({d}%)'
    },

    legend: {
      bottom: '0%',
      // 修改小图标的大小
      itemWidth: 10,
      itemHeight: 10,
      // 修改图例组件的文字为 12px
      textStyle: {
        color: 'rgba(255,255,255,.5)',
        fontSize: '12'
      }
    },
    series: [{
      name: '登录测试结果分布',
      type: 'pie',
      // 这个radius可以修改饼形图的大小
      // radius 第一个值是内圆的半径 第二个值是外圆的半径
      radius: ['45%', '70%'],
      center: ['50%', '60%'],
      avoidLabelOverlap: true,
      // 图形上的文字
      label: {
        show: false,
        position: 'center'
      },
      // 链接文字和图形的线是否显示
      labelLine: {
        show: false
      },
      itemStyle: {
        normal: {
          label: {
            formatter: function (params) {
              if (params.name !== '') {
                return params.name + '：' + params.value + '\n(' + params.percent + '%' + ')'
              } else {
                return ''
              }
            }
          },
          labelLine: {
            length: 20,
            length2: 5
          }
        }

      },
      data: [{
          value: datas.failNums,
          name: '失败'
        },
        {
          value: datas.passNums,
          name: '通过'
        },
        {
          value: datas.blockNums,
          name: '跳过'
        }
      ]
    }]
  }
  // 3. 把配置项给实例对象
  myChart.setOption(option)
  // 4. 让图表跟随屏幕自动的去适应
  window.addEventListener('resize', function () {
    myChart.resize()
  })
}

export function MapTwo(myCharts, datas) {
  var scaleData = [{
      'name': '通过',
      'value': datas.passNums
    },
    {
      'name': '失败',
      'value': datas.failNums
    },
    {
      'name': '跳过',
      'value': datas.blockNums
    }
  ]
  var rich = {
    white: {
      color: '#ddd',
      align: 'center',
      padding: [3, 0]
    }
  }
  var placeHolderStyle = {
    normal: {
      label: {
        show: false
      },
      labelLine: {
        show: false
      },
      color: 'rgba(0, 0, 0, 0)',
      borderColor: 'rgba(0, 0, 0, 0)',
      borderWidth: 0
    }
  }
  var data = []
  var color = ['#ffe000', '#00cfff', '#006ced']
  for (var i = 0; i < scaleData.length; i++) {
    data.push({
      value: scaleData[i].value,
      name: scaleData[i].name,
      itemStyle: {
        normal: {
          borderWidth: 8,
          shadowBlur: 20,
          borderColor: color[i],
          shadowColor: color[i]
        }
      }
    }, {
      value: 2,
      name: '',
      itemStyle: placeHolderStyle
    })
  }
  var seriesObj = [{
    name: '支付接口分析',
    type: 'pie',
    clockWise: false,
    avoidLabelOverlap: true,
    radius: [50, 50],
    hoverAnimation: false,
    itemStyle: {
      normal: {
        label: {
          show: true,
          position: 'outside',
          color: '#ddd',
          formatter: function (params) {
            var percent = 0
            var total = 0
            for (var i = 0; i < scaleData.length; i++) {
              total += scaleData[i].value
            }
            percent = ((params.value / total) * 100).toFixed(0)
            if (params.name !== '') {
              return params.name + '：' + params.value + ' (' + '{white|' + percent + '%}' + ')'
            } else {
              return ''
            }
          },
          rich: rich
        },
        labelLine: {
          length: 20,
          length2: 10,
          show: true,
          color: '#00ffff'
        }
      }
    },
    data: data
  }]
  var option = {
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b} : {c} ({d}%)'
    },
    legend: {
      show: false
    },
    toolbox: {
      show: false
    },
    series: seriesObj
  }
  myCharts.setOption(option)
  // 监听浏览器缩放，图表对象调用缩放resize函数
  window.addEventListener('resize', function () {
    myCharts.resize()
  })
}

export function MapThree(myCharts, datas) {
  var option = {
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b} : {c} ({d}%)'
    },
    color: ['#00ccff', '#f35833', '#8fc31f'],
    legend: {
      orient: 'horizontal',
      x: 'right',
      data: ['通过', '失败', '跳过'],
      textStyle: {
        color: '#fff'
      }
    },
    series: [{
      name: '用户接口分析',
      type: 'pie',
      radius: '55%',
      center: ['50%', '60%'],
      avoidLabelOverlap: true,
      data: [{
          value: datas.passNums,
          name: '通过'
        },
        {
          value: datas.failNums,
          name: '失败'
        },
        {
          value: datas.blockNums,
          name: '跳过'
        }
      ],
      itemStyle: {
        normal: {
          label: {
            show: true,
            formatter: '{b} : {c} ({d}%)'
          }
        },
        labelLine: {
          show: true
        },
        emphasis: {
          shadowBlur: 10,
          shadowOffsetX: 0,
          shadowColor: 'rgba(0, 0, 0, 0.5)'
        }
      }
    }]
  }
  // 3. 把配置项给实例对象
  myCharts.setOption(option)
  // 4. 让图表跟随屏幕自动的去适应
  window.addEventListener('resize', function () {
    myCharts.resize()
  })
}

